<template>
  <div class="app-container">
      <div class="login">
          <h1>监控系统</h1>
          <input v-model="loginName" type="text" placeholder="输入账号" />
          <input v-model="loginPwd" type="password" placeholder="输入密码" />
          <span class="submit" @click="gologin">登录</span>
      </div>
  </div>
</template>

<script>
import {request} from "./../lib/util";
import router from '@/router'
export default {
  name: "login",
  data() {
    return {
        loginName:"",
        loginPwd:"",
    };
  },
  methods: {
      gologin(){
          request("/login","post",{
            name:this.loginName,
            pwd:this.loginPwd
          }).then((res)=>{
            document.cookie= "token="+res.token;
            router.push({
              path: "/"
            });
          }).catch(err=>{
            alert(err.data.message);
          })
      }
  }
};
</script>


<style>
body{
  padding: 0px;
  margin:0px;
}
h1{
  color: #fff;
  font-size: 22px;
  text-align: center;
  line-height: 22px;
}
.app-container{
  position: absolute;
  width: 100%;
  height: 100%;
  background: linear-gradient(#6586ff, #23ffc2);
}
.login{
  border-radius: 10px;
  background: linear-gradient(#23ffc2,#6586ff);
  width: 300px;
  position: absolute;
  right: 50px;
  top: 20%;
  padding: 50px;
}
.login input{
  outline: gray;
  box-sizing:border-box;
  width: 100%;
  height: 36px;
  border-radius: 12px;
  margin-bottom: 10px;
  padding: 6px;
  padding-left: 12px;
  border: 0px;
}
.login input :focus{
  border: 0px;
  background-color: red;
}
.login .submit{
  font-weight: normal;
  text-align: center;
  display: block;
  width: 100%;
  float: right;
  background: linear-gradient(#fff,rgb(42, 253, 253));
  color: #7a8eff;
  border-radius: 15px;
  height: 36px;
  font-size: 18px;
  line-height: 36px;
  border: gray solid 1px;
  cursor:pointer;
}
.login .submit:hover {
  background: linear-gradient(#fff,rgb(60, 243, 243));
}
.login .submit:active{
  background: linear-gradient(#fff,rgb(30, 235, 235));
}
</style>


