import axios from 'axios';
let _axios = axios.create({
    withCredentials:true,
    baseURL: "",
    headers:{
        'X-Requested-With': 'XMLHttpRequest',
        'Content-Type':'application/json'
    },
    validateStatus: function(status) {
        return status == 200;
    },
    responseType: "json",
    maxContentLength:200000,
});

export function request(url,method,param){
    switch(method){
        case "get":
            return new Promise((resolve,reject)=>{
                return _axios.get(url,{params:param}).then(response=>{
                    resolve(response.data)
                }).catch(error=>{
                    reject(error);
                })
            })
        break;
        case "post":
            return new Promise((resolve,reject)=>{
                return _axios.post(url,param).then(response=>{
                    resolve(response.data)
                }).catch((error)=>{
                    reject(error.response);
                })
            })
        break;
        case "delete":
            return new Promise((resolve,reject)=>{
                return _axios.delete(url,{params:param}).then(response=>{
                    resolve(response.data)
                }).catch(error=>{
                    reject(error);
                })
            })
        break;
    }
}


